<template>
    <div class="fieldAnalyse">
        <!--  -->
        <my-loading :loadingVisible="loadingVisible">
            <a-row :gutter="[24,24]" style="margin:0">
                <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;" v-for="field of showList" :key="field.id"
                       class="clearfix">
                    <label class="componentTitleCls ar fl">{{field.cname}}</label>
                    <div class="componentContentCls fl clearfix">
                        <div class="fl">
                            <a-select class="frontCls" v-model="searchParam[field.ename]['type']" show-search
                                      v-if="field.isFrontComponent">
                                <a-select-option value="include">包含</a-select-option>
                                <a-select-option value="notInclude">不包含</a-select-option>
                            </a-select>
                            <a-select class="frontCls" v-model="searchParam[field.ename]['smode']" show-search
                                      v-if="field.searchType==='fuzzy'&&field.cname==='请求ip'">
                                <a-select-option value="yes">ip</a-select-option>
                                <a-select-option value="no">网段</a-select-option>
                            </a-select>
                            <a-select class="frontCls" v-model="searchParam[field.ename]['smode']" show-search
                                      v-if="field.searchType==='fuzzy'&&field.cname!=='请求ip'">
                                <a-select-option value="yes">精确</a-select-option>
                                <a-select-option value="no">模糊</a-select-option>
                            </a-select>
                        </div>
                        <div v-if="field.componentType === 'input'" class="fl"
                             :style="{width:field.isFrontComponent||field.searchType?'calc(100% - 92px)':'100%'}">
                            <a-input type="text" v-model="searchParam[field.ename]['value']"
                                     :placeholder="'请输入'+field.cname" style="width"></a-input>
                        </div>
                        <div v-if="field.componentType === 'select'" class="fl"
                             :style="{width:field.isFrontComponent||field.searchType?'calc(100% - 92px)':'100%'}">
                            <a-select show-search :placeholder="'请选择'+field.cname"
                                      v-model="searchParam[field.ename]['value']" allowClear>
                                <a-select-option :value="option.value" v-for="option of field.options"
                                                 :key="option.value">{{option.label}}
                                </a-select-option>
                            </a-select>
                        </div>
                        <div v-if="field.componentType === 'm-select'"
                             :style="{width:field.isFrontComponent||field.searchType?'calc(100% - 92px)':'100%'}"
                             class="fl">
                            <a-select mode="multiple" show-search :placeholder="'请选择'+field.cname"
                                      v-model="searchParam[field.ename]['value']" allowClear>
                                <a-select-option :value="option.value" v-for="option of field.options"
                                                 :key="option.value">{{option.label}}
                                </a-select-option>
                            </a-select>
                        </div>
                        <!-- <div v-if="field.componentType === 'textarea'" class="fl" :style="{width:field.isFrontComponent?'calc(100% - 92px)':'100%'}"></div>
                        <div v-if="field.componentType === 'num'" class="fl" :style="{width:field.isFrontComponent?'calc(100% - 92px)':'100%'}">
                          <a-input-number :min="0"></a-input-number>
                        </div> -->
                        <div v-if="field.componentType === 'day'" class="fl"
                             :style="{width:field.isFrontComponent||field.searchType?'calc(100% - 92px)':'100%'}">
                            <a-range-picker style="width:100%"
                                            v-model="searchParam[field.ename]['value']"></a-range-picker>
                        </div>
                        <!-- <div v-if="field.componentType === 'time'" class="fl" :style="{width:field.isFrontComponent?'calc(100% - 92px)':'100%'}">
                          <a-date-picker style="width:100%"  v-model="searchParam[field.ename]['value']"></a-date-picker>
                        </div> -->
                        <div v-if="field.componentType === 'date'" class="fl"
                             :style="{width:field.isFrontComponent||field.searchType?'calc(100% - 92px)':'100%'}">
                            <a-date-picker style="width:100%;" show-time
                                           v-model="searchParam[field.ename]['value']"></a-date-picker>
                        </div>
                        <div v-if="field.componentType === 'daterange'" class="fl"
                             :style="{width:field.isFrontComponent||field.searchType?'calc(100% - 92px)':'100%'}">
                            <!--<a-range-picker show-time style="width:100%" v-model="searchParam[field.ename]['value']"></a-range-picker>-->
                            <a-time-picker style="width:40%;"
                                           v-model="searchParam[field.ename]['value'][0]"></a-time-picker>
                            至
                            <a-time-picker style="width:40%;"
                                           v-model="searchParam[field.ename]['value'][1]"></a-time-picker>
                        </div>
                    </div>
                </a-col>
            </a-row>
            <hr/>
            <a-button @click="showAll = !showAll" class="show-more"
                      style="display: block;margin: -8px auto auto;background-color: #40a9ff;color: white;">{{word}}
            </a-button>
            <a-row v-if="fields.length > 0">
                <div class="ar buttons" style="padding:0 24px">
                    <a-button type="primary" @click="search(3)" :disabled="cacheFlag">
                        <a-icon type="file-search"></a-icon>
                        结果中查询
                    </a-button>
                    <a-button type="primary" @click="search(0)">
                        <a-icon type="search"></a-icon>
                        查询
                    </a-button>
                    <a-dropdown>
                        <a-menu slot="overlay">
                            <a-menu-item @click="search(1)">
                                <a-icon type="download"/>
                                快速导出
                            </a-menu-item>
                            <!--<a-menu-item  @click="search(2)"><a-icon type="export"/>离线导出</a-menu-item>-->
                            <a-menu-item @click="showModal">
                                <a-icon type="export"/>
                                离线导出
                            </a-menu-item>
                        </a-menu>
                        <a-button style="margin-left: 8px">导出
                            <a-icon type="down"></a-icon>
                        </a-button>
                    </a-dropdown>
                </div>
                <a-modal title="离线下载配置" :visible="visible" :confirm-loading="confirmLoading" @ok="handleOk"
                         @cancel="handleCancel" class="modalCls">
                    <a-form-model>
                        <a-form-model-item label="自定义文件名" prop="description">
                            <a-input placeholder="请输入文件名" rows="4" v-model="fileName"></a-input>
                        </a-form-model-item>
                        <!-- <a-form-model-item label="选择去重字段:">
                           <a-select
                                   class="searchContentCss"
                                   filterable
                                   remote
                                   default-value="a"
                           >
                             <a-select-option value="a" v-for="item of ruleGroupData" :key="item.id">{{item}}
                             </a-select-option>
                           </a-select>
                         </a-form-model-item>-->
                    </a-form-model>
                </a-modal>
            </a-row>
        </my-loading>
    </div>
</template>
<script>
    import axios from "axios";
    import ARow from "ant-design-vue/es/grid/Row";

    export default {
        name: "fieldAnalyse",
        components: {ARow},
        data() {
            return {
                interfaceData: [],
                fields: [],
                loadingVisible: false,
                searchParam: {},
                that: this,
                showAll: false, // 展开/收起筛选按钮
                MoalText: "弹框内容",
                visible: false,
                confirmLoading: false,
                // ruleGroupData:["a","b","c"],
                fileName: ""
            };
        },
        props: {
            source: {
                type: Array,
                required: true,
            },
            curObj: {
                type: String
            },
            cacheFlag:{
                type:Boolean
            }
        },
        methods: {
            // 下拉框的options来源是接口
            getOptionByApi(method, url, param) {
                return new Promise((resolve) => {
                    let newParam = Object.assign({}, param, {
                        unit: sessionStorage.getItem('selectUnitEn'),
                        anaObject: this.curObj
                    });
                    if (method === "get" || method === "GET") {
                        axios.get(url, {params: newParam}).then((res) => {
                            if (res.data.code === 1000) {
                                let optionData = res.data.data.map(item => {
                                    return {
                                        label: item,
                                        value: item
                                    }
                                });
                                resolve(optionData);
                            } else {
                                resolve([])
                            }
                        });
                    } else {
                        axios.post(url, newParam).then((res) => {
                            if (res.data.code === 1000) {
                                let optionData = res.data.data.map(item => {
                                    return {
                                        label: item,
                                        value: item
                                    }
                                });
                                resolve(optionData);
                            } else {
                                resolve([])
                            }
                        });
                    }
                });
            },
            // 根据接口名称查询接口数据
            getInterfaceByName(name) {
                return new Promise(resolve => {
                    this.$API.getInterfaceByName(name).then((res) => {
                        if (res.code === 1000) {
                            resolve(res.data)
                        } else {
                            resolve([])
                        }
                    });
                }).then(res => {
                    return this.getOptionByApi(res.method, res.url, JSON.parse(res.params));
                })
            },
            // 点击查询
            search(num) {
                // this.searchParam['analysisObject']="coremail"
                let paramObj = {};
                Object.keys(this.searchParam).forEach(key => {
                    let field = this.fields.find(list => list.ename === key);
                    let value = this.searchParam[key].value;
                    if (field.componentType === 'input' && field.isMoreValue) {
                        value = this.searchParam[key].value ? this.searchParam[key].value.split(',') : []
                    }
                    if (field.componentType === 'date') {
                        value = this.$moment(this.searchParam[key].value).format("YYYY-MM-DD hh:mm:ss")
                    }
                    if (field.componentType === 'day' && this.searchParam[key].value.length === 2) {
                        value = [this.$moment(this.searchParam[key].value[0]).format("YYYY-MM-DD"), this.$moment(this.searchParam[key].value[1]).format("YYYY-MM-DD")]
                    }
                    // if(field.componentType === 'daterange' && this.searchParam[key].value.length===2){
                    if (field.componentType === 'daterange') {
                        if (this.$moment(this.searchParam[key].value[0]).format("YYYY-MM-DD HH:mm:ss").split(' ')[1] === "date") {
                            value = [];
                        } else if (this.$moment(this.searchParam[key].value[1]).format("YYYY-MM-DD HH:mm:ss").split(' ')[1] === "date") {
                            value = [];
                        } else if (this.searchParam[key].value[0] === undefined && this.searchParam[key].value[1] === undefined) {
                            value = [];
                        } else {
                            // value = [this.$moment(this.searchParam[key].value[0]).format("YYYY-MM-DD hh:mm:ss"),this.$moment(this.searchParam[key].value[1]).format("YYYY-MM-DD hh:mm:ss")]
                            value = [this.$moment(this.searchParam[key].value[0]).format("YYYY-MM-DD HH:mm:ss").split(' ')[1], this.$moment(this.searchParam[key].value[1]).format("YYYY-MM-DD HH:mm:ss").split(' ')[1]]
                        }
                    }
                    paramObj[key] = {
                        type: this.searchParam[key].type,
                        smode: this.searchParam[key].smode,
                        value
                    }
                });
                this.$emit("exportType", num);
                if (!num) {
                    this.$emit("search", paramObj);
                }else if(num===3){
                    this.$emit("search", paramObj);
                }else {
                    this.$emit("exportFileName", this.fileName);
                    this.$emit("export", paramObj);
                }
            },
            resetParam(source) {
                let data = source.filter(list => list.isSearch === 1);
                let obj = {};
                data.forEach(list => {
                    let value;
                    // 处理默认值
                    switch (list.componentType) {
                        case 'm-select':
                            value = list.defaultValue ? list.defaultValue.split(",") : [];
                            break;
                        case 'date':
                            value = list.defaultValue ? this.$moment().format("YYYY-MM-DD hh:mm:ss") : null;
                            break;
                        case 'day':
                            value = [];
                            break;
                        case 'daterange':
                            switch (list.defaultValue) {
                                case "today":
                                    value = this.$fun.timerFormatter("today", this)
                                    break;
                                case "threeDays":
                                    value = this.$fun.timerFormatter("threeDays", this)
                                    break;
                                case "sevenDays":
                                    value = this.$fun.timerFormatter("sevenDays", this)
                                    break;
                                case "thirtyDays":
                                    value = this.$fun.timerFormatter("thirtyDays", this)
                                    break;
                                case "threeMonth":
                                    value = this.$fun.timerFormatter("threeMonth", this)
                                    break;
                                case "halfYear":
                                    value = this.$fun.timerFormatter("halfYear", this)
                                    break;
                                case "year":
                                    value = this.$fun.timerFormatter("year", this)
                                    break;
                                default:
                                    value = []
                                    break;
                            }
                            break;
                        default:
                            value = list.defaultValue
                            break;
                    }
                    obj[list.ename] = {
                        type: 'include',
                        smode: "yes",
                        value,
                    }
                });
                this.searchParam = obj
                // console.log(this.searchParam)
            },

            // 离线下载弹框
            showModal() {
                this.visible = true;
                this.fileName = "";
            },
            handleOk() {
                this.search(2);
                this.visible = false;
            },
            handleCancel() {
                this.visible = false;
            },
        },
        watch: {
            'source': {
                // 不适用computed的原因是在computed里面需要异步，并且完全依赖data里面的数据变化 造成computed的执行滞后于created和mounted周期
                // 去处理字段数据中的options 先拿到当前字段options的接口api，然后根据api的相关配置去请求该api拿到数据，去替换字段中的options
                handler: function (newSource) {
                    console.log("__________________",newSource)
                    this.loadingVisible = true;
                    let data = newSource.filter((list) => list.isSearch === 1);
                    data.forEach(async list => {
                        if (list.componentType === "select" || list.componentType === "m-select") {
                            // 是下拉框和下拉多选框
                            if (list.selectComponentDataType === 2) {
                                // 接口api
                                list.options = await this.getInterfaceByName(list.options);
                            } else {
                                // console.log(list)
                                list.options = JSON.parse(list.options);
                            }
                        }
                    });
                    this.resetParam(newSource);
                    this.fields = data;
                    this.search(0);
                    this.loadingVisible = false;
                }
            }
        },
        computed: {
            showList: function () {
                if (this.showAll === false) {                    // 当数据不需要完全显示的时候
                    var showList = [];   // 定义一个空数组
                    if (this.fields.length > 6) {   // 这里我们先显示前三个
                        for (var i = 0; i < 6; i++) {
                            showList.push(this.fields[i])
                        }
                    } else {
                        showList = this.fields
                    }
                    return showList;  // 返回当前数组
                } else {
                    return this.fields;
                }
            },
            word: function () {
                if (this.showAll === false) {  //对文字进行处理
                    return '展开'
                } else {
                    return '收起'
                }
            }
        },
        created() {
            // this.resetParam(this.source);
        },
    };
</script>
<style>
    .componentTitleCls {
        width: 82px;
        padding-right: 18px;
        line-height: 32px;
        font-size: 14px;
        color: #e8e8e8;
    }

    .componentContentCls {
        width: calc(100% - 82px);
    }

    .fieldAnalyse .frontCls {
        width: 82px;
        margin-right: 10px;
        color: #ffc53d;
    }

    .fieldAnalyse .buttons button {
        margin: 0 20px;
        min-width: 100px;
    }
</style>