<template>
  <a-dropdown v-model="isVisible">
    <div class="buttonBorder">
      <a-button type="primary"><a-icon type="appstore"></a-icon>自定义列</a-button>
    </div>
    <a-menu slot="overlay">
      <a-menu-item :key="item.dataIndex"  v-for="item in check_config">
        <a-checkbox style="color: white" v-model="item.is_checke">{{item.title}}</a-checkbox>
      </a-menu-item>
    </a-menu>
  </a-dropdown>
</template>

<script>
  export default {
    name: "DynamicColumn",
    data() {
      return {
        check_config: [],
        isVisible:false,
      }
    },
    props: {
      all_column: {
        type: Array,
        default: function () {
          return {}
        }
      }
    },
    watch: {
      check_config: {
        handler() {
          this.$emit('on-change', this.check_config);
        },
        deep: true
      },
        all_column: {
            handler() {
                this.convert_column_config();
            },
            deep: true
        }
    },
    methods: {
      convert_column_config() {
          this.check_config = [];
            this.all_column.forEach((item,index) => {
                let _item = {
                    title: item.title,
                    dataIndex: item.dataIndex,
                    is_checke:index<=9,
                    ellipsis:true,
                };
                this.check_config.push(_item);
            })
      },
    },
    mounted() {
      this.convert_column_config();
    },
  }
</script>

