<template>
    <div class="logSearch">
        <a-spin tip="导出中..." :spinning="exportSpin">
            <div class="searchArea" style="padding-top: 10px">
                <fieldAnalyse :source="currentFields" :cacheFlag="cacheFlag" @search="search" @exportType="exType"
                              @exportFileName="getFileName"
                              @export="exportData"></fieldAnalyse>
            </div>
            <a-divider></a-divider>
            <div>
                <div class="tableTool clearfix" style="color:#faad14">
                    <div class="fl" style="line-height: 38px;font-weight: 700;">工具栏：</div>
                    <div class="fl titleFilter">
                        <DynamicColumn :all_column="columns" @on-change="dynamic_change_column"></DynamicColumn>
                    </div>
                    <a-divider type="vertical" style="margin:0 20px;height:38px;" class="fl"></a-divider>
                    <div class="fl" style="line-height: 38px;font-weight: 700;">区域选择：</div>
                    <a-radio-group v-model="analyseParam.area" @change="areaChange" class="fl"
                                   style="line-height:38px;">
                        <a-radio :value="-1">全部</a-radio>
                        <a-radio :value="0">境内外均访问</a-radio>
                        <a-radio :value="2">境外访问</a-radio>
                        <a-radio :value="1">境内访问</a-radio>
                    </a-radio-group>
                    <a-divider type="vertical" style="margin:0 20px;height:38px;" class="fl"></a-divider>
                    <div class="fl" style="line-height: 38px;font-weight: 700;">结果统计（ <span style="color:#1890ff">{{result.dateList[0] || ""}} ~ {{result.dateList[1] || ""}} </span>）：
                    </div>
                    <ul class="fl clearfix tongji" style="margin-bottom:0;padding:0;line-height:38px;">
                        <li class="fl">
                            <!--邮箱（<a @click="detailClick('email',result.emailTotal)">{{result.emailTotal}}</a>）-->
                            邮箱/用户名（<a @click="detailClick('email',result.emailTotal)">查看详情</a>）
                        </li>
                        <li class="fl">
                            <!--城市（<a @click="detailClick('city',result.cityTotal)">{{result.cityTotal}}</a>）-->
                            城市（<a @click="detailClick('city',result.cityTotal)">查看详情</a>）
                        </li>
                        <li class="fl">
                            <!--IP（<a @click="detailClick('ip',result.ipTotal)">{{result.ipTotal}}</a>）-->
                            IP（<a @click="detailClick('ip',result.ipTotal)">查看详情</a>）
                        </li>
                        <li class="fl">
                            <!--国家（<a @click="detailClick('country',result.countryTotal)">{{result.countryTotal}}</a>）-->
                            国家（<a @click="detailClick('country',result.countryTotal)">查看详情</a>）
                        </li>
                    </ul>
                </div>
                <div class="table">
                    <a-table :columns="detailsColumns" :dataSource="logData" rowKey="id" @change="tableChange"
                             :loading="loadingVisible" :pagination="false"></a-table>
                    <div>
                        <a-button type="primary" style="float: right;margin: 1% auto auto 1%;" @click="resultSave" :loading="saveFlag">缓存结果</a-button>
                        <a-button type="danger" style="float: right;margin: 1% auto auto 1%;" @click="showConfirm" :loading="deleFlag">删除缓存</a-button>
                    </div>
                    <div class="ar clearfix" style="margin-top: 20px" v-if="logData.length > 0">
                        <a-pagination
                                class="fr"
                                v-model="pageParam.page"
                                :total="total"
                                :page-size="pageParam.size"
                                @change="pageChange"
                                simple
                                :show-total="(total) => `共${total}条`"
                        ></a-pagination>
                        <p class="fr" style="margin-right:20px">总条数：{{ total || 0 }} 条</p>
                    </div>
                </div>
                <a-modal title="统计详情" v-model="detail_visible" :width="800" class="modalCls" :okText="'导出'">
                    <a-table :columns="detail_column[currentStr]" :dataSource="detailData" :loading="detailLoading"
                             :pagination="false"></a-table>
                    <div class="ar clearfix" style="margin-top: 20px" v-if="detailData.length > 0">
                        <a-pagination
                                v-model="detailPage.page"
                                :total="detailTotal"
                                :page-size="detailPage.size"
                                @change="detailPageChange"
                                simple
                                :show-total="(total) => `共${total}条`"
                        ></a-pagination>
                    </div>
                    <template slot="footer">
                        <a @click="detail_visible=false" style="margin-right:20px;">取消</a>
                        <a-dropdown>
                            <a-menu slot="overlay">
                                <a-menu-item @click="detailExport(1)">
                                    <a-icon type="download"/>
                                    快速导出
                                </a-menu-item>
                                <!--<a-menu-item  @click="detailExport(2)"><a-icon type="export"/>离线导出</a-menu-item>-->
                                <a-menu-item @click="showModal">
                                    <a-icon type="export"/>
                                    离线导出
                                </a-menu-item>
                            </a-menu>
                            <a-button style="margin-left: 8px">导出
                                <a-icon type="down"></a-icon>
                            </a-button>
                        </a-dropdown>
                        <a-modal title="离线下载配置" :visible="visible" :confirm-loading="confirmLoading" @ok="handleOk"
                                 @cancel="handleCancel" class="modalCls">
                            <a-form-model>
                                <a-form-model-item label="自定义文件名" prop="description">
                                    <a-input placeholder="请输入文件名" rows="4" v-model="fileNameModel"></a-input>
                                </a-form-model-item>
                            </a-form-model>
                        </a-modal>
                    </template>
                </a-modal>
                <a-modal v-model="deleteVisible" :width="800" class="modalCls" :okText="'确定'" @ok="delResult">
                    <p style="font-size: 16px;">你确定删除当前缓存结果吗?</p>
                </a-modal>
            </div>
        </a-spin>
    </div>
</template>
<script>
    import fieldAnalyse from "@/components/fieldComponent/fieldAnalyse.vue";
    import DynamicColumn from "@/components/selectTable/DynamicColumn"
    import {downloadFile} from "@/config/req/api/search";

    export default {
        name: "logSearch",
        components: {
            fieldAnalyse,
            DynamicColumn
        },
        props: {
            curNode: {},
        },
        data() {
            return {
                currentFields: [],
                columns: [],
                detailsColumns: [],
                searchParam: {},
                analyseParam: {
                    dataRange: 1,
                    area: -1,
                },
                pageParam: {
                    page: 1,
                    size: 10,
                },
                logData: [],
                total: 0,
                loadingVisible: false,
                result: {
                    "emailTotal": 0,
                    "cityTotal": 0,
                    "dateList": [null, null],
                    "ipTotal": 0,
                    "countryTotal": 0
                },
                currentParam: {},
                detailData: [],
                detail_visible: false,
                detailLoading: false,
                detail_column: {
                    email: [
                        {
                            title: "用户名",
                            dataIndex: "username",
                            align: "center"
                        }
                    ],
                    ip: [
                        {
                            title: "请求ip",
                            dataIndex: "req_ip",
                            align: "center"
                        },
                        {
                            title: "国家",
                            dataIndex: "country",
                            align: "center"
                        },
                        {
                            title: "城市",
                            dataIndex: "city",
                            align: "center"
                        }
                    ],
                    city: [
                        {
                            title: "城市",
                            dataIndex: "city",
                            align: "center"
                        }
                    ],
                    country: [
                        {
                            title: "国家",
                            dataIndex: "country",
                            align: "center"
                        }
                    ]
                },
                currentStr: "",
                detailPage: {
                    page: 1,
                    size: 10,
                },
                detailTotal: 0,
                exportSpin: false,
                downloadSize: 0,
                fileName: "", // 导出文件名
                MoalText: "弹框内容",
                visible: false,
                confirmLoading: false,
                // ruleGroupData:["a","b","c"],
                fileNameModel: "",
                // 查询后返回的表名
                pageTableName:"",
                // 缓存查询按钮显示
                cacheFlag: true,
                // 删除缓存结果Flag
                deleteVisible:false,
                // 删除缓存loading
                deleFlag:false,
                // 缓存结果loading
                saveFlag:false
            };
        },
        watch: {
            curNode: {
                handler: function (node) {
                    this.searchFields(node.id, node.ownerType);
                },
            },
        },
        methods: {
            searchFields(levelId, ownerType) {
                this.$API.searchFields({levelId, ownerType}).then((res) => {
                    if (res.code === 1000) {
                        this.currentFields = res.data.filter((list) => list.isSearch === 1);
                        this.columns = this.currentFields.map((list) => {
                            return {
                                title: list.cname,
                                dataIndex: list.ename,
                                sorter: true,
                                sortOrder: "ascend"
                            };
                        });
                    } else {
                        this.currentFields = [];
                        this.columns = [];
                        this.$message.warning(res.message);
                    }
                });
            },

            // 检索
            search(param) {
                param["levelId"] = this.curNode.id;
                param["ownerType"] = this.curNode.ownerType;
                param["unit"] = sessionStorage.getItem("selectUnitEn");
                if(this.paramType===3){
                    param["tableName"] = this.pageTableName;
                }else {
                    param["tableName"] = "";
                }
                this.searchParam = param;
                this.getinAndOutData();
            },
            // 请求日志数据
            getLog(param) {
                this.loadingVisible = true;
                let finallyParam = Object.assign({}, param, this.pageParam);
                this.$API.searchLog(finallyParam).then((res) => {
                    if (res.code === 1000) {
                        this.logData = res.data;
                        this.total = res.total;
                        if(res.tableName!==""){
                            this.pageTableName = res.tableName;
                            this.cacheFlag = false
                        }else {
                            this.cacheFlag = true
                        }
                    } else {
                        this.logData = [];
                        this.total = 0;
                    }
                    this.loadingVisible = false;
                });
            },
            // 镜内外分析请求数据
            getinAndOutData() {
                let status = this.analyseParam.area;
                if (status !== -1) {
                    let belong;
                    switch (status) {
                        case 0:
                            belong = "special";
                            break;
                        case 1:
                            belong = "inchina";
                            break;
                        case 2:
                            belong = "nochina";
                            break
                    }
                    let param = Object.assign({}, this.searchParam, this.pageParam, {belong});
                    this.currentParam = param;
                    this.getLog(param);
                    // this.tjResult(param)
                } else {
                    this.currentParam = this.searchParam;
                    this.getLog(this.searchParam);
                    // this.tjResult(this.searchParam)
                }
            },
            pageChange() {
                // this.getLog(this.searchParam);
                this.getinAndOutData();
            },
            // 自定义列触发事件
            dynamic_change_column(new_columns) {
                this.detailsColumns = new_columns.filter((list) => list.is_checke);
            },
            // 区域选择变化
            areaChange() {
                if (!this.searchParam.levelId) {
                    console.error("未查询到数据");
                    return;
                }
                this.pageParam.page = 1;
                this.getinAndOutData();
            },
            // 统计查询结果
            tjResult(param) {
                this.$API.tj_result(param).then((res) => {
                    if (res.code === 1000 && Object.keys(res.data).length > 0) {
                        this.result = res.data;
                    } else {
                        this.$message.warning(res.message);
                        this.result = {
                            "emailTotal": 0,
                            "cityTotal": 0,
                            "dateList": [null, null],
                            "ipTotal": 0,
                            "countryTotal": 0
                        }
                    }
                });
            },
            // 统计结果的详情
            detailClick(str, num) {
                /*if(num === 0){
                  this.$message.warning("该条无统计结果数据")
                  return
                }*/
                console.log(num);
                this.detailPage = {
                    page: 1,
                    size: 10,
                };
                this.currentStr = str;
                this.detail_visible = true;
                this.detailAjax(str)
            },
            // 详情请求
            detailAjax(str) {
                this.detailLoading = true;
                let that = this;

                function callback(res) {
                    if (res.code === 1000) {
                        that.detailData = res.data.data;
                        that.detailTotal = res.data.total;
                    } else {
                        that.detailData = [];
                        that.$message.warning(res.message);
                    }
                    that.detailLoading = false;
                }

                let param = Object.assign({}, this.currentParam, this.detailPage);
                switch (str) {
                    case "email":
                        this.$API.detail_email(param).then(res => {
                            console.log("+_+_+_+", res);
                            this.downloadSize = res.data.total;
                            callback(res)
                        })
                        break;
                    case "ip":
                        this.$API.detail_ip(param).then(res => {
                            this.downloadSize = res.data.total;
                            callback(res)
                        })
                        break;
                    case "city":
                        this.$API.detail_city(param).then(res => {
                            this.downloadSize = res.data.total;
                            callback(res)
                        })
                        break;
                    case "country":
                        this.$API.detail_country(param).then(res => {
                            this.downloadSize = res.data.total;
                            callback(res)
                        })
                        break;
                }
            },
            // 分页器
            tableChange(pagination, filters, sorter) {
                if (this.logData.length === 0) {
                    return;
                }
                if (sorter.order) {
                    this.sortParam = {
                        field: sorter.field,
                        order: sorter.order === "descend" ? "desc" : "asc",
                    };
                } else {
                    this.sortParam = {};
                }
                this.searchParam = Object.assign({}, this.searchParam, this.sortParam);
                this.getinAndOutData();
            },
            // 获取导出文件名
            getFileName(param) {
                this.fileName = param;
            },
            // 外面导出按钮
            exType(param) {
                this.paramType = param;
            },
            exportData(param) {
                let fieldList = this.detailsColumns.map((list) => list.dataIndex);
                if (fieldList.length < 1) {
                    this.$message.warning("请至少选择一列");
                    return;
                }
                if (this.logData.length === 0) {
                    // 未检测到数据
                    this.$message.warning("未检测到数据");
                    return;
                }
                param["ownerType"] = this.curNode.ownerType;
                param["levelId"] = this.curNode.id;
                param["fieldList"] = fieldList;
                param["unit"] = sessionStorage.getItem("selectUnitEn");
                this.searchParam = param;
                let status = this.analyseParam.area;
                if (status === 1 || status === 2) {
                    // 境内或者境外
                    let finallyParam = Object.assign({}, this.searchParam, this.pageParam, {
                        belong: status === 1 ? "inchina" : "nochina",
                    });
                    if (this.paramType === 1) {
                        // 如果是快速导出
                        this.exportLog(finallyParam);
                    } else if (this.paramType === 2) {
                        // 如果是离线导出
                        this.logOfflineDownload(finallyParam, 'ordinary');
                    }
                } else if (status === -1) {
                    // 全部 没有belong
                    let finallyParam = this.searchParam;
                    if (this.paramType === 1) {
                        this.exportLog(finallyParam);
                    } else if (this.paramType === 2) {
                        this.logOfflineDownload(finallyParam, 'ordinary');
                    }
                } else {
                    // 境内外均访问 独立的导出接口
                    let finallyParam = Object.assign({}, this.searchParam, this.pageParam);
                    if (this.paramType === 1) {
                        // 快速
                        this.exportLogInOut(finallyParam);
                    } else if (this.paramType === 2) {
                        // 离线
                        this.logOffInoutDownload(finallyParam, 'ordinary');
                    }
                }
            },
            exportLog(param) {
                this.exportSpin = true;
                let finallyParam = Object.assign({}, param, {size: 100000});
                this.$API.exportLog(finallyParam).then((res) => {
                    downloadFile(res, "日志数据.csv");
                    this.exportSpin = false;
                }).catch(err => {
                    this.$message.error(err.message, 10)
                    this.exportSpin = false;
                });
            },
            exportLogInOut(param) {
                this.exportSpin = true;
                let finallyParam = Object.assign({}, param, {size: 100000});
                this.$API.exportLogInOut(finallyParam).then((res) => {
                    downloadFile(res, "日志数据.csv");
                    this.exportSpin = false;
                });
            },
            // 境内、境外、全部离线导出接口  和下面方法一样了
            logOfflineDownload(param, str) {
                this.exportSpin = true;
                let finallyParam = Object.assign(
                    {logs_types_offline: str, fileName: this.fileName},
                    param
                );
                this.$API.logOfflineDownload(finallyParam).then((res) => {
                    if (res.code === 1000) {
                        this.$message.success(res.message);
                    } else {
                        this.$message.error(res.message);
                    }
                    this.exportSpin = false;
                });
            },
            // 境内外的离线导出接口 和上面的方法一样了
            logOffInoutDownload(param, str) {
                this.exportSpin = true;
                let finallyParam = Object.assign(
                    {logs_types_offline: str, fileName: this.fileName},
                    param
                );
                this.$API.logOfflineDownload(finallyParam).then((res) => {
                    if (res.code === 1000) {
                        this.$message.success(res.message);
                    } else {
                        this.$message.error(res.message);
                    }
                    this.exportSpin = false;
                });
            },
            // 详情分页变化
            detailPageChange() {
                // this.getLog(this.searchParam);
                this.detailAjax(this.currentStr)
            },
            // 详情导出
            detailExport(num) {
                let status = this.analyseParam.area;
                // 快速导出
                let belong;
                switch (status) {
                    case 0:
                        belong = "special";
                        break
                    case 1:
                        belong = "inchina";
                        break
                    case 2:
                        belong = "nochina";
                        break
                }
                if (num === 1) {
                    if (status !== -1) {
                        // 境内外均访问 境内访问  境外访问
                        this.quicklyExport(Object.assign({fileName:this.fileNameModel}, this.searchParam, {belong}));
                    } else {
                        // 全部
                        this.quicklyExport(Object.assign({fileName:this.fileNameModel}, this.searchParam));
                    }
                } else {
                    // 离线导出
                    if (status !== -1) {
                        // 境内外均访问 境内访问  境外访问
                        let param = Object.assign({fileName:this.fileNameModel}, this.searchParam, {belong});
                        this.offlineExport(param);
                    } else {
                        // 全部
                        let param = Object.assign({fileName:this.fileNameModel}, this.searchParam);
                        this.offlineExport(param);
                    }
                }
                console.log(num)
            },
            // 详情快速导出
            quicklyExport(param) {
                let finallyParam = Object.assign({}, param, {type: this.currentStr}, {size: 100000});
                this.$API.detail_quickly(finallyParam).then(res => {
                    downloadFile(res, `${this.currentStr}.csv`);
                }).catch(err => {
                    this.$message.error(err.message, 10)
                });
            },
            // 详情离线导出
            offlineExport(param) {
                param["ownerType"] = this.curNode.ownerType;
                param["levelId"] = this.curNode.id;
                param["unit"] = sessionStorage.getItem("selectUnitEn");
                param["page"] = 1;
                param["size"] = this.downloadSize;
                this.logOfflineDownload(param, this.currentStr);
                // this.searchParam = param;
                // let status = this.analyseParam.area;
                // if (status === 1 || status === 2) {
                //   let finallyParam = Object.assign({}, param, this.pageParam, {
                //     belong: status === 1 ? "inchina" : "nochina",
                //   });
                //     this.logOfflineDownload(finallyParam);
                // } else if (status === -1) {
                //   let finallyParam = param;
                //   if (this.paramType === 1) {
                //     this.exportLog(finallyParam);
                //   } else if (this.paramType === 2) {
                //     this.logOfflineDownload(finallyParam);
                //   }
                // } else {
                //   let finallyParam = Object.assign({}, param, this.pageParam);
                //   if (this.paramType === 1) {
                //     this.exportLogInOut(finallyParam);
                //   } else if (this.paramType === 2) {
                //     this.logOffInoutDownload(finallyParam);
                //   }
                // }
            },

            // 离线下载弹框
            showModal() {
                this.visible = true;
                this.fileNameModel = "";
            },
            handleOk() {
                this.detailExport(2);
                this.visible = false;
            },
            handleCancel() {
                this.visible = false;
            },
            // 保存缓存结果
            resultSave(){
                this.saveFlag = true;
                this.$message.info("正在缓存结果");
                this.$API.saveResult(this.searchParam).then(res => {
                    this.$message.success(res.message);
                    this.saveFlag = false;
                }).catch(err => {
                    this.$message.error(err.message)
                });
            },
            // 删除缓存结果对话框
            showConfirm(){
                this.deleteVisible = true;
            },
            // 删除缓存结果
            delResult(){
                this.deleteVisible = false;
                this.deleFlag = true;
                this.$API.deleteResult().then(res => {
                    this.$message.success(res.message);
                    this.deleFlag = false;
                }).catch(err => {
                    this.$message.error(err.message)
                });
            }
        },
    };
</script>
<style>
    .titleFilter button {
        border-radius: 0;
        /* min-width: 50px; */
        font-size: px;
    }

    .buttonBorder {
        padding: 2px;
        /* border:1px solid #ccc; */
    }

    .tongji > li {
        list-style: none;
        margin: 0 20px;
        color: whitesmoke;
    }

    .logSearch .ant-pagination-simple-pager input, .modalCls .ant-pagination-simple-pager input {
        background: transparent;
        border: none;
        padding: 0;
    }
</style>